.skills {
	padding: 4rem 0 8rem 0;
	background-attachment: fixed;
	background-size: cover;
	position: relative;
	z-index: -1;

	h2 {
		font-size: 3rem;
		position: relative;
		padding: 2rem;
	}

	.skillsContainer {
		position: relative;
		p {
			width: 50%;
			font-size: 1.5rem;
			padding-left: 2rem;
		}
		.skillIcons {
			width: 80%;
			display: flex;
		.icons {
			width: 50%;
			display: flex;
			flex-direction: column;
			align-items: center;
			i {
				font-size: 3rem;
			}
			p {
				font-size: 2rem;
				text-align: center;
				}
			}
		}
	}
}

.parallax {
  perspective: 1px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}
.parallax__layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.parallax__layer--base {
  transform: translateZ(0);
}
.parallax__layer--back {
  transform: translateZ(-1px);
}
